import React from 'react';
import { createMuiTheme } from '@material-ui/core';
import { ThemeProvider } from '@material-ui/core/styles';
import { Route } from 'react-router-dom';
import MainPage from './pages/MainPage';
import { useSelector } from 'react-redux';
import HeaderContainer from './containers/common/HeaderContainer';
import SignInPage from './pages/SignInPage';
import SignUpPage from './pages/SignUpPage';
import DocumentWriterContainer from './containers/documents/DocumentWriterContainer';

const overrides = {
  MuiTab: {
    // general overrides for your material tab component here
    root: {
      backgroundColor: '#fff',
      color: 'rgb(94,199,194)',
      opacity: 0,
      '&$selected': {
        backgroundColor: 'rgb(94,199,194)',
        color: '#fff',
      },
    },
    textColorInherit: {
      color: 'rgb(94,199,194)',
    },
  },
};

const theme = createMuiTheme({
  overrides,
  palette: {
    common: { black: '#000', white: '#fff' },
    background: { paper: '#fff', default: '#fafafa' },
    primary: {
      light: 'rgba(114, 219, 214, 1)',
      main: 'rgba(94, 199, 194, 1)',
      dark: 'rgba(73, 179, 173, 1)',
      contrastText: 'rgba(254, 255, 255, 1)',
    },
    secondary: {
      light: '#ff4081',
      main: '#f50057',
      dark: '#c51162',
      contrastText: '#fff',
    },
    error: {
      light: '#e57373',
      main: '#f44336',
      dark: '#d32f2f',
      contrastText: '#fff',
    },
    text: {
      primary: 'rgba(95, 95, 95, 0.87)',
      secondary: 'rgba(95, 95, 95, 0.54)',
      disabled: 'rgba(0, 0, 0, 0.38)',
      hint: 'rgba(0, 0, 0, 0.38)',
    },
  },
  props: {
    // Name of the component ⚛️
    PrivateSwitchBase: { root: { disableRipple: true } }, // No more ripple, on the whole application 💣!},
  },
});
function App() {
  const { user } = useSelector(({ user }) => ({ user: user.user }));

  return (
    <>
      <ThemeProvider theme={theme}>
        <HeaderContainer user={user} />
        <Route path="/" component={MainPage} exact />
        <Route path="/auth/login" component={SignInPage} />
        <Route path="/auth/register" component={SignUpPage} />
        <Route path="/docs/write" component={DocumentWriterContainer} />
      </ThemeProvider>
    </>
  );
}

export default App;
