import client from './client';

// 로그인
export const login = ({ email, password }) => client.post('/api/auth/login', { email, password });

export const snsLogin = ({ provider }) => client.get(`/api/auth/${provider}`);

// 회원 가입
export const register = sendData =>
  client.post('/api/auth/register', {
    email: sendData.email,
    password: sendData.password,
    personal: sendData.personal,
    business: sendData.business,
    userType: sendData.userType,
  });

export const check = () => client.get('/api/auth/check');

export const logout = () => client.post('/api/auth/logout');
