import { createAction, handleActions } from 'redux-actions';
import produce from 'immer';
//import produce from 'immer';
//import {takeLatest} from 'redux-saga/effects';
// import createRequestSaga, {createRequestActionTypes} from '../../lib/redux/createRequestSaga';

const initialState = {
  title: '',
  desc: '',
  characters: [
    {
      id: 'character-0',
      name: '',
      address: '',
      telephone: '',
      partyRelations: '',
    },
    {
      id: 'character-1',
      name: '',
      address: '',
      telephone: '',
      partyRelations: '',
    },
  ],
  state: 0,
  litigatePlanType: 0,
  fileList: [],
};

const WRITE_DOCUMENTS = 'docs/WRITE_DOCUMENTS';

const ADD_CHARACTERS = 'docs/ADD_CHARACTERS';
const MODIFY_CHARACTERS = 'docs/MODIFY_CHARACTERS';
const DELETE_CHARACTERS = 'docs/DELETE_CHARACTERS';

export const writeDocument = createAction(
  WRITE_DOCUMENTS,
  ({ key, value }) => ({ key, value }),
);

export const addCharacters = createAction(ADD_CHARACTERS, id => ({ id }));
export const modifyCharacters = createAction(
  MODIFY_CHARACTERS,
  (id, key, value) => ({ id, key, value }),
);

export const deleteCharacters = createAction(DELETE_CHARACTERS, id => ({ id }));

const docs = handleActions(
  {
    [WRITE_DOCUMENTS]: (state, { payload: { key, value } }) =>
      produce(state, draft => {
        draft[key] = value;
      }),
    [ADD_CHARACTERS]: (state, { payload: { id } }) => ({
      ...state,
      characters: state.characters.concat({
        id: `character-${id}`,
        name: '',
        address: '',
        telephone: '',
        partyRelations: '',
      }),
    }),
    [MODIFY_CHARACTERS]: (state, { payload: { id, key, value } }) =>
      produce(state, draft => {
        const targetIndex = draft.characters.findIndex(
          character => character.id === id,
        );
        draft.characters[targetIndex][key] = value;
      }),
    [DELETE_CHARACTERS]: (state, { payload: { id } }) =>
      produce(state, draft => {
        const targetIndex = draft.characters.findIndex(
          character => character.id === id,
        );

        draft.characters.splice(targetIndex, 1);
      }),
  },
  initialState,
);

export default docs;
