import React, { useEffect, useCallback, useState } from 'react';
import SignInForm from '../../components/auth/SignInForm';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../../modules/user_modules/auth';
import { check } from '../../modules/user_modules/user';
import AuthTemplate from '../../components/auth/AuthTemplate';
const SignInContainer = ({ history }) => {
  const { auth, authError, user } = useSelector(({ auth, user }) => ({
    auth: auth.auth,
    authError: auth.authError,
    user: user.user,
  }));

  const [error, setError] = useState(null);

  const [loginData, setLoginData] = useState({ email: '', password: '' });
  const dispatch = useDispatch();

  const onChange = useCallback(
    e => {
      setLoginData({
        ...loginData,
        [e.target.name]: e.target.value,
      });
    },
    [loginData],
  );

  const onSubmit = useCallback(
    e => {
      e.preventDefault();
      dispatch(login(loginData));
    },
    [dispatch, loginData],
  );

  useEffect(() => {
    if (authError) {
      console.log('오류 발생');
      console.log(authError);
      setError('로그인 실패');
      return;
    }
    if (auth && !(auth.personal || auth.business)) {
      history.push('/auth/register');
      return;
    } else if (auth) {
      console.log('로그인 성공');
      dispatch(check());
    }
  }, [auth, authError, dispatch, history]);

  useEffect(() => {
    if (user) {
      history.push('/');
      try {
        localStorage.setItem('user', JSON.stringify(user));
      } catch (e) {
        console.log('localStorage is not working');
      }
    }
  }, [user, history]);

  return (
    <AuthTemplate type="sign-in" onSubmit={onSubmit}>
      <SignInForm onChange={onChange} />
    </AuthTemplate>
  );
};

export default SignInContainer;
