import React, { useEffect, useCallback, useState } from 'react';
import SignUpForm from '../../components/auth/SignUpForm';
import AuthTemplate from '../../components/auth/AuthTemplate';

import produce from 'immer';
import { useDispatch } from 'react-redux';

import { register } from '../../modules/user_modules/auth';

const SignUpContainer = () => {
  const dispatch = useDispatch();

  const [user, setUser] = useState({
    email: '',
    password: '',
    personal: {
      mobile: '',
    },
    business: {
      companyNumber: '',
      companyName: '',
      companyRepName: '',
    },
    userType: 'personal',
  });

  const [userKind, setUserKind] = useState(0);

  const onChange = useCallback(
    e => {
      const targetName = e.target.name;
      const targetValue = e.target.value;

      setUser(state =>
        produce(state, draft => {
          if (targetName === 'email' || targetName === 'password') {
            draft[targetName] = targetValue;
          } else {
            draft[state.userType][targetName] = targetValue;
          }
        }),
      );
      console.log(user);
    },
    [user],
  );

  const handleChange = (_, newValue) => {
    setUserKind(newValue);
    setUser({
      ...user,
      userType: newValue === 0 ? 'personal' : 'business',
    });
  };

  const onSubmit = e => {
    e.preventDefault();

    const sendData = {
      email: user.email,
      password: user.password,
      userType: user.userType,
    };

    if (userKind === 0) {
      sendData['personal'] = {
        ...user['personal'],
      };
    } else {
      sendData['business'] = {
        ...user['business'],
      };
    }

    dispatch(register(sendData));
  };

  return (
    <AuthTemplate type="sign-up" onSubmit={onSubmit}>
      <SignUpForm
        onChange={onChange}
        handleChange={handleChange}
        userKind={userKind}
      />
    </AuthTemplate>
  );
};

export default SignUpContainer;
