import React from 'react';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CommonTextField from '../common/CommonTextField';

export default function SignInForm({onChange}) {
  return (
    <>
      <CommonTextField
        required
        fullWidth
        label="이메일을 입력하세요"
        name="email"
        autoComplete="email"
        autoFocus
        onChange={onChange}
      />
      <CommonTextField
        required
        fullWidth
        name="password"
        label="비밀번호를 입력하세요"
        type="password"
        autoComplete="current-password"
        onChange={onChange}
      />
      <FormControlLabel
        control={<Checkbox value="remember" color="primary" />}
        label="아이디를 저장할게요"
      />
    </>
  );
}
