import React from 'react';
import Container from '@material-ui/core/Container';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  root: {
    display: 'flex',
    justifyContent: 'center',
    height: '100vh',
    marginTop: '4rem',
  },
};

const CenteredContainerRaw = props => {
  const { classes, color, ...other } = props;
  return <Container color={color} className={classes.root} {...other} />;
};

const CenteredContainer = withStyles(styles)(CenteredContainerRaw);

export default CenteredContainer;
