import React from 'react';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import CommonTextField from '../../common/CommonTextField';
import DeleteIcon from '@material-ui/icons/Delete';
import { useDispatch } from 'react-redux';
import { deleteCharacters } from '../../../modules/document_modules/docs';

const useStyles = makeStyles(theme => ({
  root: {
    marginBottom: theme.spacing(1),
    padding: theme.spacing(3),
    display: 'flex',
    flexDirection: 'column',
  },
  addressButton: {
    margin: theme.spacing(1),
  },
  addressInput: {
    flexGrow: 1,
  },
}));

const CharacterCard = ({ handleChangeCharacter, character }) => {
  const classes = useStyles();

  const dispatch = new useDispatch();

  return (
    <Paper elevation={2} className={classes.root}>
      <CommonTextField
        placeholder="이름"
        name="name"
        onChange={event => handleChangeCharacter(character.id, event)}
      />
      <Box
        style={{
          display: 'flex',
          alignItems: 'baseline',
        }}
      >
        <CommonTextField
          className={classes.addressInput}
          placeholder="주소"
          name="address"
          onChange={event => handleChangeCharacter(character.id, event)}
        />
        <Button variant="outlined" className={classes.addressButton}>
          주소 찾기
        </Button>
      </Box>
      <CommonTextField
        placeholder="당사자 관계"
        name="partyRelations"
        onChange={event => handleChangeCharacter(character.id, event)}
      />
      <DeleteIcon onClick={() => dispatch(deleteCharacters(character.id))} />
    </Paper>
  );
};

export default CharacterCard;
