import React from 'react';
import ReactFullpage from '@fullpage/react-fullpage';
import { Typography } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import { Link, withRouter } from 'react-router-dom';

import Stepper from '@material-ui/core/Stepper';
import StepLabel from '@material-ui/core/StepLabel';

import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
}));

function getSteps() {
  return ['설명 1', '설명 2', '설명 3'];
}

const MainComponent = () => {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div
      style={{
        height: '100vh',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
      }}
    >
      <Stepper
        alternativeLabel={true}
        activeStep={activeStep}
        orientation="vertical"
        className={'stepper'}
        style={{
          position: 'absolute',
          zIndex: 999,
        }}>
        {steps.map((label, index) => (
          <StepLabel key={label}>{label}</StepLabel>
        ))}
      </Stepper>
      <ReactFullpage
        style={{
          zIndex: 0,
        }}
        licenseKey={'429A04CD-66844896-815D8221-BF45DFB9'}
        fixedElements="#header, .footer .stepper"
        navigation={true}
        onLeave={(origin, destination, direction) => {
          if (direction === 'down') {
            handleNext();
          } else {
            handleBack();
          }
        }}
        render={({ state, fullpageApi }) => {
          return (
            <ReactFullpage.Wrapper>
              <div
                className="section fp-auto-height-responsive"
                style={{ backgroundImage: `url('/images/page3.jpg')` }}
              >
                <Container
                  style={{
                    display: 'flex',
                    alignItems: 'center',
                    flexDirection: 'column',
                  }}
                >
                  <Typography variant={'h3'} style={{ color: 'white' }}>
                    준비 되셨나요? 무료로 내용증명 작성을 체험해 보세요!
                  </Typography>
                  <Container
                    maxWidth="xl"
                    style={{
                      backgroundColor: 'white',
                      display: 'flex',
                      justifyContent: 'space-around',
                    }}
                  >
                    <Link to={'/docs/aggrement'}>Button Link</Link>
                    <Link to={'/auth/register'}>Button Link</Link>
                  </Container>
                </Container>
              </div>

              <div
                className="section fp-auto-height-responsive"
                style={{ backgroundImage: `url('/images/page1.jpg')` }}
              >
                <Container
                  style={{ display: 'flex', justifyContent: 'center' }}
                >
                  <Typography variant={'h3'} style={{ color: 'white' }}>
                    설명 1
                  </Typography>
                </Container>
              </div>
              <div
                className="section fp-auto-height-responsive"
                style={{ backgroundImage: `url('/images/page2.jpg')` }}
              >
                <Container
                  style={{ display: 'flex', justifyContent: 'center' }}
                >
                  <Typography variant={'h3'} style={{ color: 'white' }}>
                    설명 2
                  </Typography>
                </Container>
              </div>
            </ReactFullpage.Wrapper>
          );
        }}
      />
    </div>
  );
};

export default withRouter(MainComponent);
