import React, { useRef, useCallback } from 'react';
import WriteDocument from '../../components/documents/WriteDocument';
import { useDispatch, useSelector } from 'react-redux';
import {
  addCharacters,
  modifyCharacters,
  writeDocument,
} from '../../modules/document_modules/docs';

const DocumentWriterContainer = () => {
  const dispatch = useDispatch();

  const characterNumber = useRef(2);

  const { characters, fileList } = useSelector(({ docs }) => ({
    characters: docs.characters,
    fileList: docs.fileList,
  }));

  const handleAddCharacter = useCallback(() => {
    dispatch(addCharacters(characterNumber.current));
    characterNumber.current += 1;
  }, [characterNumber, dispatch]);

  const handleChangeCharacter = useCallback(
    (id, event) => {
      dispatch(modifyCharacters(id, event.target.name, event.target.value));
    },
    [dispatch],
  );

  const handleChangeDocument = useCallback(
    e => {
      dispatch(
        writeDocument({
          key: e.target.name,
          value: e.target.value,
        }),
      );
    },
    [dispatch],
  );

  return (
    <>
      <WriteDocument
        handleAddCharacter={handleAddCharacter}
        handleChangeCharacter={handleChangeCharacter}
        handleChangeDocument={handleChangeDocument}
        characters={characters}
        fileList={fileList}
      />
    </>
  );
};

export default DocumentWriterContainer;
